.messageWindow-enter {
  // animation: show-messageWindow 500ms forwards;
  opacity: 0;
  transform: scale(0.8);
}
.messageWindow-enter-active {
  opacity: 0.3;
  transform: scale(1);
  transition: opacity 700ms, transform 700ms;
}

.messageWindow-exit {
  opacity: 1;
}
.messageWindow-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 700ms, transform 700ms;
}



//===================================================
// messageText
.messageText-enter {
  animation: show-messageText 700ms forwards;
}
.messageText-enter-active {

}
.messageText-exit {
  opacity: 1;
}
.messageText-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

@keyframes show-messageText{
  0%{
    transform: translateY(1000px);
  }
  50%{
    transform: translateY(-60px);
  }
  80%{
    transform: translateY(10px);
  }  

  100%{
    transform: translateX(0px);

  }
}

