$widthMessage: 600px;
$heightMessage: 500px;

$widthEventMessageComponentText: 550px;
$heightEventMessageComponentText: 100px;

$widthEventMessageComponentInput: 650px;;
$heightEventMessageComponentInput: 100px;

$widthEventMessageComponentButtons: 550px;;
$heightEventMessageComponentButtons: 100px;

.eventMessage {
  position: fixed;
  left: calc(50% - $widthMessage/2);
  top: calc(32% - $heightMessage/2);
  width: $widthMessage;
  height: $heightMessage;
  border-radius: 20px;
  border: 3px solid black;
  
  background-color: rgba(255, 255, 255, 0.207);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px 1px;
  z-index: 50;


}
.eventMessageComponent-text{
  position: fixed;
  left: calc(50% - $widthEventMessageComponentText/2);
  top: calc(25% - $heightEventMessageComponentText/2);
  width: $widthEventMessageComponentText;
  height: $heightEventMessageComponentText;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  // background-color: red;
  border: 3px solid black;
  border-radius: 30px;
  z-index: 51;
}
.eventMessageComponent-input{
  position: fixed;
  left: calc(50% - $widthEventMessageComponentInput/2);
  top: calc(34% - $heightEventMessageComponentInput/2);
  width: $widthEventMessageComponentInput;
  height: $heightEventMessageComponentInput;
  z-index: 51;
  border-radius: 30px;
   border: 3px solid black;
   
  // background-color: rgb(97, 18, 18);
}
.eventMessageComponent-buttons{
  position: fixed;
    left: calc(50% - $widthEventMessageComponentButtons/2);
    top: calc(43% - $heightEventMessageComponentButtons/2);
    width: $widthEventMessageComponentButtons;
    height: $heightEventMessageComponentButtons;
    z-index: 51;
    border-radius: 30px;
    // border: 3px solid black;
    // background-color: aqua;
    display: flex;
    justify-content: space-between;

  &__button{
    background-color: inherit;
    border-radius: 30px;
    width: 250px;
    height: 100%;
    border: 3px solid black;
    font-weight: 600;
    font-size: 30px;
    text-align: center;
    line-height: 100px;



  }
    
}

.blackFonMessage{ // background fon for every pop-up message
  position: fixed;
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.689);
  z-index: 48;
  
}
